"use client";

import Image from "next/image";
import clsx from "clsx";

const images = Array.from(
  { length: 13 },
  (_, i) => `/pics/sliderImages/image ${i + 1}.png`
);

const Slider = () => {
  return (
    <div className="flex flex-col md:flex-row h-[300px] md:h-auto w-full overflow-hidden gap-4">
      <ImageSlider
        images={images}
        direction="normal"
        orientation="horizontal"
        className="w-full md:w-1/2 h-40 md:h-[600px]"
      />
      <ImageSlider
        images={images}
        direction="reverse"
        orientation="horizontal"
        className="w-full md:w-1/2 h-40 md:h-[600px]"
      />
    </div>
  );
};

type ImageSliderProps = {
  images: string[];
  direction: "normal" | "reverse";
  orientation: "horizontal" | "vertical";
  className?: string;
};

const ImageSlider = ({
  images,
  direction,
  orientation,
  className,
}: ImageSliderProps) => {
  const isVertical = orientation === "vertical";

  return (
    <div
      className={clsx(
        "relative overflow-hidden",
        className,
        "group",
        isVertical ? "flex-col" : "flex-row"
      )}
    >
      <div
        className={clsx(
          "flex absolute",
          isVertical ? "flex-col h-max w-full" : "flex-row w-max h-full",
          "animate-slide",
          direction === "reverse" && "animate-reverse"
        )}
        style={{
          animationDuration: "20s",
        }}
      >
        {[...images, ...images].map((src, index) => (
          <div
            key={index}
            className={clsx(
              isVertical ? "h-40 w-full" : "w-40 h-full",
              "flex-shrink-0"
            )}
          >
            <Image
              src={src}
              alt={`slider-img-${index}`}
              width={200}
              height={200}
              className="object-cover w-full h-full"
            />
          </div>
        ))}
      </div>
    </div>
  );
};

export default Slider;
